import React from 'react';
import { Card, Alert, List, Typography, Badge } from 'antd';

const { Text, Paragraph } = Typography;

const ResultView = ({ analysis, reviewLoading }) => {
    if (reviewLoading) {
        return (
            <Card title="" bordered={false} loading={true}>
                ...
            </Card>
        )
    }

    if (!analysis) {
        return (
            <Card title="" bordered={false}>
                <Text type="secondary"></Text>
            </Card>
        );
    }

    const { risk_clauses, action_advice } = analysis;

    const renderRiskTag = (level) => {
        switch (level) {
            case '': return <Badge status="error" text="" />;
            case '': return <Badge status="warning" text="" />;
            default: return <Badge status="success" text="" />;
        }
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>
            <Card title={` ${risk_clauses.length} `} bordered={false}>
                {risk_clauses.length === 0 && <Alert message="" type="success" showIcon />}
                <List
                    itemLayout="vertical"
                    dataSource={risk_clauses}
                    renderItem={(item) => (
                        <List.Item style={{ background: item.risk_level === '' ? '#fff1f0' : '#fffbe6', padding: '16px', borderRadius: '8px', marginBottom: '16px' }}>
                            <List.Item.Meta
                                title={renderRiskTag(item.risk_level)}
                                description={<Text strong>{item.original_text}</Text>}
                            />
                            <Paragraph><strong>:</strong> {item.risk_reason}</Paragraph>
                            <Alert message="" description={item.suggestion} type="info" showIcon />
                        </List.Item>
                    )}
                />
            </Card>

            <Card title="" bordered={false} style={{ background: '#f6ffed' }}>
                <Text>{action_advice}</Text>
            </Card>
        </div>
    );
};

export default ResultView;
