import React, { useState } from 'react';
import { Card, Input, Button, Typography, message } from 'antd';
import { CopyOutlined, DownloadOutlined, SafetyCertificateOutlined } from '@ant-design/icons';
import axios from 'axios';
import { API_BASE_URL } from '../config';

const { TextArea } = Input;
const { Title } = Typography;

const LetterGenerator = () => {
    const [disputeType, setDisputeType] = useState('deposit_refund');
    const [context, setContext] = useState('');
    const [generatedLetter, setGeneratedLetter] = useState('');
    const [loading, setLoading] = useState(false);

    const generateLetter = async () => {
        if (!context) {
            message.warning('');
            return;
        }

        setLoading(true);
        setGeneratedLetter('');

        try {
            const response = await axios.post(`${API_BASE_URL}/api/generate_letter`, {
                dispute_type: disputeType,
                context: context
            });
            setGeneratedLetter(response.data.letter_content);
            message.success('');
        } catch (error) {
            console.error('Generation Error:', error);
            message.error('');
        } finally {
            setLoading(false);
        }
    };

    const handleCopy = () => {
        navigator.clipboard.writeText(generatedLetter);
        message.success('');
    };

    const handleDownload = () => {
        const element = document.createElement("a");
        const file = new Blob([generatedLetter], { type: 'text/plain' });
        element.href = URL.createObjectURL(file);
        element.download = "legal_letter.txt";
        document.body.appendChild(element); // Required for this to work in FireFox
        element.click();
        message.success('');
    };



    return (
        <Card title={<span><SafetyCertificateOutlined /> 3.  (AI)</span>} bordered={false}>

            <div style={{ marginBottom: 16 }}>
                <label style={{ display: 'block', marginBottom: 8 }}></label>
                <select
                    style={{ width: '100%', padding: '8px', marginBottom: 16, borderRadius: '4px', border: '1px solid #d9d9d9' }}
                    value={disputeType}
                    onChange={(e) => setDisputeType(e.target.value)}
                >
                    <option value="deposit_refund"></option>
                    <option value="salary_arrears"></option>
                    <option value="consumer_rights"></option>
                </select>

                <label style={{ display: 'block', marginBottom: 8 }}></label>
                <TextArea
                    rows={3}
                    placeholder="5000"
                    value={context}
                    onChange={(e) => setContext(e.target.value)}
                    style={{ marginBottom: 16 }}
                />

                <Button type="primary" onClick={generateLetter} loading={loading} block>
                    {loading ? '...' : ''}
                </Button>
            </div>

            {generatedLetter && (
                <div style={{ background: '#f0f2f5', padding: '16px', borderRadius: '8px' }}>
                    <Title level={5}></Title>
                    <pre style={{ whiteSpace: 'pre-wrap', fontFamily: 'inherit' }}>{generatedLetter}</pre>
                    <div style={{ marginTop: 16, display: 'flex', gap: '8px' }}>
                        <Button icon={<CopyOutlined />} onClick={handleCopy}></Button>
                        <Button icon={<DownloadOutlined />} onClick={handleDownload}></Button>
                    </div>
                </div>
            )}
        </Card>
    );
};

export default LetterGenerator;
